$colorArrow: #ff3e79;
$colorWhite: #ffffff;
$colorLightGray: #f8f8f8;

.section-services {
  border-top: 1px solid #d9fff5;
  border-top: 1px solid #d9fff5;
  background-color: $colorLightGray;

  .section-services_header {
    background: linear-gradient(to right, $colorLightGray, $colorWhite);

    .section-services_header__title {
      color: #7e7e82;
      text-transform: uppercase;
      line-height: 24px;
      font-weight: normal;
      font-weight: 100;
    }
  }

  .swiper-slide {
    background: $colorWhite;
    display: flex !important;
    justify-content: space-between;
    align-items: center;
    height: 72px;
    border: 1px solid #eaeaea;
    border-radius: 7px;
    background-color: white;
    padding-left: 17px;
    padding-right: 17px;

    .swiper-slide:hover {
      box-shadow: 0px 5px 5px 0px #9fa0a6;
    }
    .swiper-slide:nth-child(n + 11) {
      display: flex;
    }

    img {
      display: block;
    }
    .swiper__arrow {
      position: relative;
      height: 40px;
      width: 40px;
      border: 2px solid $colorArrow;
      border-radius: 50%;
      box-sizing: border-box;
    }
    .swiper__arrow::after {
      position: absolute;
      content: "";
      right: 16px;
      top: 12px;
      height: 9px;
      width: 2px;
      background-color: $colorArrow;
      transform: rotate(-45deg);
      border-radius: 5px;
    }
    .swiper__arrow::before {
      position: absolute;
      content: "";
      right: 16px;
      top: 17px;
      height: 9px;
      width: 2px;
      background-color: $colorArrow;
      transform: rotate(45deg);
      border-radius: 5px;
    }
  }
  .swiper-pagination {
    bottom: 26px !important;
    width: 100%;
    height: 10px;
    z-index: 10;

    span {
      background-color: #979797;
      height: 12px;
      width: 12px;
    }
  }
  .swiper-second .swiper-slide {
    height: 160px;
    padding: 15px;
    flex-direction: column;
    align-items: flex-start;
  }
  .swiper-second .swiper__arrow {
    align-self: flex-end;
  }

  .order {
    border: none;
    bottom: 16px;
    align-self: flex-end;
    position: absolute;
    width: 119px;
    height: 32px !important;
    color: white;
    background-color: $colorArrow;
    border-radius: 50px;
    text-transform: uppercase;
    padding-left: 12px;
    text-align: start;
    font-weight: bold;
    box-sizing: border-box;
  }
  .order::before {
    position: absolute;
    content: "";
    height: 8px;
    width: 2px;
    background-color: white;
    border-radius: 5px;
    transform: rotate(-45deg);
  }
  .order::after {
    position: absolute;
    content: "";
    height: 8px;
    width: 2px;
    background-color: white;
    border-radius: 5px;
    transform: rotate(45deg);
  }
  .slider-third {
    .swiper-slide__block {
      line-height: 18px;
      font-size: 14px;
    }
    .swiper-slide__names {
      color: #7e7e82;
      margin-bottom: 4px;
    }
    .swiper-slide__description {
      font-size: 14px !important;
    }
  }
}
.section-services_footer__text {
  line-height: 20px;
  font-size: 14px;
  padding-bottom: 16px;
}

@media (max-width: 768px) {
  .section-services_header {
    padding-top: 16px;
    padding-left: 16px;
    padding-bottom: 16px;
  }
  .swiper {
    height: 114px;
    padding: 16px !important;
    padding-top: 16px;

    .swiper-slide {
      box-sizing: border-box;
      width: 240px;
    }
    .swiper-slide:nth-child(n + 11) {
      display: none !important;
    }
    .swiper__button {
      display: none;
    }
  }

  .swiper-second {
    height: 200px;
  }

  .slider-third {
    height: 230px;

    .order::before {
      top: 10px;
      right: 19px;
    }
    .order::after {
      top: 14px;
      right: 19px;
    }
  }
  .slider-third .swiper-slide {
    width: 260px;
    height: 200px;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px;
  }

  .section-services_footer {
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 40px;

    button {
      width: 200px;
      border: none;
      background-color: transparent;
      padding: 0;
      text-align: start;
      font-size: 16px;
      font-weight: 700;
      position: relative;
    }
    button:before {
      position: absolute;
      content: "";
      height: 10px;
      width: 2px;
      background-color: $colorArrow;
      right: 80px;
      top: 21px;
      transform: rotate(-45deg);
      border-radius: 5px;
    }
    button:after {
      position: absolute;
      content: "";
      height: 10px;
      width: 2px;
      background-color: $colorArrow;
      right: 80px;
      top: 27px;
      transform: rotate(45deg);
      border-radius: 5px;
    }
  }
}
@media (min-width: 769px) and (max-width: 1365px) {
  .section-services_header {
    padding-top: 15px;
    padding-left: 24px;
    padding-bottom: 15px;
  }
  .swiper {
    padding: 24px;

    .swiper-wrapper {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      gap: 16px 24px;
      max-height: 160px;
      overflow: hidden;
      padding-bottom: 15px;
      transform: translate3d(0px, 0px, 0px) !important;
    }
  }
  .swiper__button {
    padding-left: 8px;
    background-color: transparent;
    border: none;
    font-size: 16px;
    color: #1b1c21;
    margin-top: 0px;
    // border: 1px solid red;

    img {
      margin-right: 9px;
    }
  }
  .open img {
    transform: rotate(180deg);
  }
  .swiper-slide {
    box-sizing: border-box;
    max-width: 224px;
    margin-right: 0 !important;
  }
  .slider-third {
    padding-top: 0;

    .swiper-wrapper {
      display: table;
      flex-direction: column;
      align-items: flex-start;
      padding-bottom: 0;
      position: relative;
      border-radius: 10px;
    }
    .swiper-slide {
      border: none;
      display: table-cell;
      padding: 16px;
      max-width: 100%;
      border-radius: 0;

      .order {
        position: static;
      }
      .order::before {
        margin-top: 0.5px;
        right: 30px;
      }
      .order::after {
        margin-top: 5.5px;
        right: 30px;
      }
      div:nth-child(1) {
        width: 63%;
        margin-right: auto;
      }
      div:nth-child(2) {
        width: 15%;
        margin-left: auto;
      }
      div:nth-child(3) {
        width: 16.5%;
        margin-left: auto;
      }
    }
    .swiper-slide:nth-child(n + 2) {
      position: relative;
    }
    .swiper-slide:nth-child(n + 2):before {
      position: absolute;
      content: "";
      border-top: 1px solid #eaeaea;
      height: 10px;
      width: 10px;
      width: 96%;
      top: 0;
    }
    .swiper-pagination {
      display: none;
    }
    .order {
      position: absolute;
      right: 18px;
    }
    .swiper-slide:first-child {
      position: relative;
    }
    .swiper-slide .swiper-slide__names {
      display: none;
    }
    .swiper-slide__table-head {
      display: flex;
      padding-top: 30px;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 16px;

      .order {
        position: static;
        visibility: hidden;
      }
      p:nth-child(1) {
        width: 63%;
        margin-right: auto;
      }
      p:nth-child(2) {
        width: 15%;
        margin-left: auto;
      }
      p:nth-child(3) {
        width: calc(16.5% + 120px);
      }
    }
  }
  .section-services_footer {
    padding-left: 24px;
    padding-right: 24px;
    padding-bottom: 45px;

    .section-services_footer__text {
      width: 65%;
    }
    button {
      width: 400px;
      border: none;
      background-color: transparent;
      padding: 0;
      text-align: start;
      font-size: 16px;
      font-weight: 700;
      position: relative;
    }
    button:before {
      position: absolute;
      content: "";
      height: 10px;
      width: 2px;
      background-color: $colorArrow;
      right: 80px;
      top: 2px;
      transform: rotate(-45deg);
      border-radius: 5px;
    }
    button:after {
      position: absolute;
      content: "";
      height: 10px;
      width: 2px;
      background-color: $colorArrow;
      right: 80px;
      top: 8px;
      transform: rotate(45deg);
      border-radius: 5px;
    }
  }
}
